* {
    margin: 0;
    padding: 0;
}

body {
    background-image: url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQt1ddD9cnacfi2vJSHXyBPn7HW_GYsXQ373g&s);
}

.App-input {
    padding-top: 20px;
    text-align: center;
    background: url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTkhsX-yZX4xjNLhtdDCd1YZAdX_PFHP4MOTQ&s);
    height: 280px;
    color: navy;
    font-size: 18px;
}

.App-input input {
    font-size: 14px;
    border-radius: 5px;
    border: 1px solid grey;
    outline: none;
    padding: 10px 5px;
    width: 200px;
}

.App-input button {
    background-color: rgb(134, 7, 108);
    color: white;
    margin-right: 20px;
    padding: 10px 20px;
    border-radius: 30px;
    margin-top: 10px;
}

nav {
    background-color: rgba(24, 64, 195, 0.45);
    padding: 10px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2px;
}

nav ul {

    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    color: pink;
}

nav ul li {
    list-style-type: none;
    cursor: pointer;
    font-size: 14px;
    padding-left: 100px;
    font-weight: bold;
    transition: al 0.5s case-in-out;
    color: white;
}

nav ul li:hover {
    transform: scale(1.3);
}

.navActive {
    color: skyblue;
}